/**  W N E S
 * "[["S5", "S4", "S3", "S2", "H5", "H4", "H3", "D5", "D4", "D3", "C5", "C4", "C3"], 
 *   ["S8", "S7", "S6", "HA", "HK", "HQ", "DA", "DK", "DQ", "CA", "CK", "CQ", "C2"],
 *   ["SJ", "ST", "S9", "HJ", "HT", "H9", "DJ", "DT", "D9", "D2", "CJ", "CT", "C9"], 
 *   ["SA", "SK", "SQ", "H8", "H7", "H6", "H2", "D8", "D7", "D6", "C8", "C7", "C6"]
 * ]"
 * 
 * N E S W
 * const deals = 'K34.J3.Q742.K832 XXX.XX.XXXX.XXXX QJ98.A5.J853.QT4 XXX.XX.XXXX.XXXX';
 */
import ReactDOM from 'react-dom';
import React, { Component } from 'react';
import tableStore from '../stores/tableStore';
import  { ACT1 } from '../components/Card';
import ResultPanel from '../views/pc/ResultPanel';
const Cards = {
    W:["S5", "S4", "S3", "S2", "H5", "H4", "H3", "D5", "D4", "D3", "C5", "C4", "C3"],
    N:["S8", "S7", "S6", "HA", "HK", "HQ", "DA", "DK", "DQ", "CA", "CK", "CQ", "C2"],
    E:["SJ", "ST", "S9", "HJ", "HT", "H9", "DJ", "DT", "D9", "D2", "CJ", "CT", "C9"],
    S:["SA", "SK", "SQ", "H8", "H7", "H6", "H2", "D8", "D7", "D6", "C8", "C7", "C6"]
}
Array.prototype.remove = function(val) {
    var index = this.indexOf(val);
    if (index > -1) {
        this.splice(index, 1);
    }
};
const Player = ["E","S","W","N"]
let Current = [];

let Record = []
class GameProcess {
    start(){
        console.log("start")
        tableStore.dplay("W", "S5");
        
    }
    play = (item)=>{
        const {card,seat} = item
        Current.push(card)
        Record.push(card)
        this.dealPlay(seat,card)
    }

    dealPlay = (seat,card) =>{
        Cards[seat].remove(card)
       if(Record.length===1) tableStore.openDummy("N",["S8", "S7", "S6", "HA", "HK", "HQ", "DA", "DK", "DQ", "CA", "CK", "CQ", "C2"])
       if(Current.length === 4) Current = [];
       let nextPlayer = this.nextPlayer(seat)
       let suit = Current.length === 0 ? 'SHDC' : Current[0][0]
       this.rule(nextPlayer,suit)
       if(Record.length===52){
        
      
        const result = document.querySelector('.result');
        if (!result)
         ReactDOM.render(<ResultPanel />, document.querySelector('#result'))
       }
    }
    nextPlayer = (seat) =>{
        let next = ""
        if(Current.length !==4)  next = Player[(Player.indexOf(seat) + 1 )% 4] ;
        return next
    }
    rule = (next,suit)=>{
        if("SN".indexOf(next) !== -1){
            let cards = tableStore.selectCards(next, suit, [ACT1.L]);
            if (cards.length == 0) {
            cards = tableStore.selectCards(next, 'SHDC', [ACT1.L]);
            tableStore.setCardsState(cards, { active: ACT1.LC, onclick: tableStore.play });

            } else {
            tableStore.setCardsState(cards, { active: ACT1.LC, onclick: tableStore.play });
            var reg = new RegExp(suit, "g");
            var a = 'SHDC'.replace(reg, "");
            cards = tableStore.selectCards(next, a, [ACT1.L]);
            tableStore.setCardsState(cards, { active: ACT1.D, onclick: tableStore.play });
            }
        }else{
           let delay = 10;
            if(Current.length===0) delay = 1500
            setTimeout(()=>{
                tableStore.dplay(next, Cards[next][0]);
            },delay)
            
        }
    }
}

export default new GameProcess
