import React from 'react';
import TweenOne from 'rc-tween-one';

/**
 * 
 */
export default (props) => (
  <TweenOne
    animation={{
      ...props.animation,
      ease: 'easeOutQuint',       //   easeOutExpo
    }}
    style={props.style ? props.style : null}
    className={props.className ? props.className : null}
  >
    {props.children}
  </TweenOne>
)