// C8,C4,C2,C3  
// SQ,S8,SK     

function genUserCards(deal){
    const suits="SHDC";
    const hands = deal.split(" ");
    return hands.map((hand)=>{
        let h = [];
        hand.split(".").forEach((suit,i)=>{
            suit.split("").forEach((card)=>h.push(suits[i]+card))
        })
        return h;
    });
}





