import React from 'react'
import { Modal, Button, Input } from 'antd';
import { useState,useCallback } from 'react'
import 'antd/dist/antd.css'

// function AlertForm(){
//   const [isShow, setIsShow] = useState(false);
//   return(
//     <h1>333333333</h1>
//   );
// }

function AlertForm({bid,callback}) {
  const [isShow, setIsShow] = useState(true)
  const msgRef = React.useRef(null);
  const cb = useCallback((value)=>{
    setIsShow(false);
    callback(msgRef.current.state.value);
  });

  return (
    <div>
      <Modal
        title={`${bid}`}
        visible={isShow}
        mask={false}
        closable={false}
        onOk={cb}
        onCancel={cb}
        footer={[
          <Button type="primary" onClick={()=>cb(1)}></Button>,
          <Button onClick={()=>cb(0)}></Button>
        ]}
        style={{top:'20vh',left:'-30vh'}}
      >
      <Input ref={msgRef} placeholder="" />
      </Modal>
    </div>
  );
}

export default AlertForm;