/**
 * TableView 
 *    table
 *    
 * 
 * Debug 
 */
import React from 'react';
import Debug from './Debug'
import Claim from './Claim'
import BidPanel from './BidPanel'
import { Seats, Tricks } from './Headers'
import Prepare from './Prepare'
import UserTags from './UserTag'
import Timer from './Timer'
import Card from '../../components/Card';
import Record from '../../components/Record';
import './TableView.css'
import { inject, observer } from 'mobx-react';
import InfoDrawer from './InfoDrawer';
/**
 *  table  tableview 
  */
const _tableObj = {
  cards: [],
  state: {
    scene: 0,
    calldata: [['1C', '2C', 'PASS', 'PASS'], ['3H', 'PASS', 'PASS', '4NT'], ['PASS', 'PASS', 'PASS', '']],
    user: {
      E: { ready: 0, name: '', face: '/imgs/face1.png', rank: '' },
      S: { ready: 0, name: '', face: '/imgs/face2.png', rank: '' },
      W: { ready: 0, name: '', face: '/imgs/face1.png', rank: '' },
      N: { ready: 0, name: '', face: '/imgs/face2.png', rank: '' }
    },
  },
  ref: { E: null, S: null, W: null, N: null },
  openDebug: e => null,
  debug: e => null,
  lastTrick: e => null,
  timer: { stop: e => null, start: e => null },
  claim: e => null,
  bid: e => null,
}


/**
 * TableView 
 *  TableView   view1,2,3
 *  Table  render 
 *  copy  this  props.table 
 * 
 * 
 * //const table = _tableObj;
 */
@inject('tableStore')
@observer
class TableView extends React.Component {
  /*  props  view 
    view
    
  */
  render() {
    console.log(111)
    const props = this.props;
    const table = props.table;
    const tableStore = props.tableStore;
    window.__tableStore = tableStore
    let cards = tableStore.state.cards;
    cards = cards ? Card.createComponents(cards) : null;
    const stat = Object.values(tableStore.state.user).map(e => e.ready);
    const tricks = {
      winEW:tableStore.state.winEW,
      winSN:tableStore.state.winSN,
      contract:tableStore.state.contract,
      declarer:tableStore.state.declarer
    }
    const claimBtnClickable =   tableStore.state.claimAble && tableStore.state.declarer===tableStore.myseat;
    const hcp = tableStore.scene === 1 ? tableStore.hcp : null;
    return (
      <div>
        {(tableStore.state.scene===1) ?
          <div className='panel'>
            <BidPanel />
          </div> : null
        }
        <div id='table' className='table'>
          <div id='header' className='header'>
            {/* <div className='re imps'><Imps /></div> */}
            <div onClick={table.openDebug} className='re seats'><Seats dealer={tableStore.dealer} logicDealer={tableStore.logicDealer} vul={tableStore.state.vulnerable} sequence={tableStore.sequence} /></div>
            <div onClick={table.lastTrick.bind(table)} className='re tricks'><Tricks data={tricks} /></div>
            <div className='re time'>
              <Timer
                name=''
                handle={table.timer}
                time= {90}
                callback={() => console.log('')} />
            </div>
            <button onClick={claimBtnClickable ? table.claim :()=>{}} className={claimBtnClickable?"claimbtn":"claimbtn disable"} ></button>
            <button onClick={table.lastTrick.bind(table)} className="lasttrick"></button>
            <button onClick={table.showInfo}></button>
            <button onClick={table.history.bind(table)} className="showbid"></button>
            {/* <div className='re' id='lastTrick'></div>*/}
            {/* div */}
            <div id='result'></div>
            <div id='sound'></div>
          </div>

          <div id='body' className='body'>
            {tableStore.state.lastTrick ? <div id='lastTrick' className='lastTrick'>33333</div> : null}
            {/* onSubmit  */}
            {tableStore.state.scene === 3 ?
              <Claim number='8' onSubmit={table.handleClaim} /> 
              : null}
            <div id='clock'></div>
            <div id='north' className='north' ref={table.ref.N}></div>
            <div id='east' className='east' ref={table.ref.E}></div>
            <div id='south' className='south' ref={table.ref.S}></div>
            <div id='west' className='west' ref={table.ref.W}></div>
            <div id='board' className='board' ref={table.ref.board}>
              <UserTags user={tableStore.state.user} myseat={tableStore.myseat} hcp={hcp}/>
              {tableStore.state.scene === 0 ? <Prepare stat={stat} ready={table.handleReady} /> : null}
            </div>
            {cards}
          </div>
          {tableStore.state.debug ? <Debug o={table} /> : null}
          <Record/>
          <InfoDrawer/>
          <div id='footer' className='footer'>
            <div id='video'></div>
            <div id='userlist'>
              <table>
                <tr>
                  <td>1</td><td></td><td></td>
                </tr>
                <tr>
                  <td>2</td><td></td><td></td>
                </tr>
                <tr>
                  <td>3</td><td></td><td></td>
                </tr>
                <tr>
                  <td>4</td><td></td><td></td>
                </tr>
              </table>
            </div>
            <div id='message' className='message'>
              <div id='msg'></div>
              <div>
                <input id='msginput' type='text' defaultValue='' />
                {/* <input id='msgbtn' type='button' value='' onClick={table.testChat} /> */}
              </div>
            </div>
            <div id='advertising'></div>
          </div>
          <div id='ad'></div>
        </div>
      </div >
    );
  }

}
export default TableView;