import React from 'react';
import Motion from '../libs/Motion'
//import TweenOne from 'rc-tween-one';
//* active define 0,1,2,3  0 1 2 , 3 
// D dark, L light,LC light click,LCO light click out
const ACT0 = 0;                             // initCards
const ACT1 = { D: 1, L: 2, LC: 3, LCO: 4 }  // dealCards
const ACT2 = 5;                             // play() in board
const ACT3 = 6;                             // out of Screen

/**
 *  Card 
 *      props.position
 *      props.animation
 *      5S  XS 
 *      40,123
 *      
 * 
 * props 
 *      animation    TweenOne  animation 
 *      position    
 *      size        
 *      card        5S 5
 *      zIndex      
 *      onClick     , onclick() 
 *      active      0,1,2  0 1 2 , 3 
 * 
 * 
 *       ../lib/Motion
 * 
 * 
 * 
 *      X XHXHXH,XDXD,XSXSXS  
*/
class Card extends React.Component {
    handleClick = () => false;
    setActive(act) {
        switch (act) {
            case ACT1.D:
                this.props.animation && (this.props.animation['brightness'] = 0.6);
                break;
            case ACT1.L:
                this.props.animation && (this.props.animation['brightness'] = 1)
                break;
            case ACT1.LC:
            case ACT1.LCO:
                this.handleClick = this.props.onClick;
                this.props.animation && (this.props.animation['brightness'] = 1)
                break;
            case ACT2:
            case ACT3:
                this.handleClick = () => false;
                this.props.animation && (this.props.animation['brightness'] = 1)
                break;
            default:
                break;
        }
    }
    render() {
        // 
        const getStyle = () => {
            let style = { position: 'absolute' }
            if (this.props.position) {
                style['top'] = this.props.position.y + 'px'
                style['left'] = this.props.position.x + 'px'
            }
            if (this.props.size) {
                style['height'] = this.props.size * 1;
                style['width'] = this.props.size * 0.7;
            }
            return style;
        }
        //  active  card 
        this.setActive(this.props.active);
        const card = this.props.card.slice(-1) === 'X' ?   // XH  XS XD 
            'back' : this.props.card;


        return (
            <div id={'card' + this.props.index}  // TODO: div zIndex 
                style={{
                    position: 'absolute',
                    zIndex: this.props.zIndex,
                }}
            >
                <Motion animation={this.props.animation} style={getStyle()}>
                    <img onClick={this.handleClick(`${this.props.index}`)}
                        // onDragStart={(e)=>e.preventDefault()}
                        alt={this.props.key}
                        src={`cards/${card}.svg`}
                        style={{
                            position: 'absolute',
                            width: "100%",
                            height: "100%",
                            // zIndex: this.props.zIndex,
                        }}
                    />
                </Motion>
            </div>
        )
    }
}
/**
 *   cards  React 
 */
Card.createComponents = function (cards) {
    return cards.map((item1, index1) => {
        return item1.map((item2, index2) => {
            return <Card
                active={item2.active}
                onClick={item2.onclick}
                key={item2.key}
                index={item2.key}
                seat={item2.seat}
                animation={item2.animation || ''}
                card={item2.card}
                size={item2.size}
                position={item2.position}
                zIndex={item2.zIndex}
            />
        });
    });
}
Card.suits = ['S', 'H', 'C', 'D']; window.__Card=Card

export default Card
export {ACT0,ACT1,ACT2,ACT3}

