import React from 'react';
import { inject, observer } from 'mobx-react';
import {cardImgs,imgPreLoad} from '../common/util';
import Table from './Table';
/**
 * Game  
 * Table  
 */
@inject('tableStore')

@observer
class Game extends React.Component {
    constructor(props) {
        /**
         *  
         *  
         */
        super(props);
        this.debug=false;
        this.init();// 
        
        window.__debug = this.debug;
    }
    /**
     * 
     * 
     */
    init() {
       
        // 
        imgPreLoad(cardImgs());

        this.props.tableStore.tableId = this.props.match.params.tableid;
        if (this.debug) return;  // 
        window.document.oncontextmenu = function () {
            
            return true;
        }
        document.ondragstart=(e)=>e.preventDefault(); // 
        document.onselectstart=(e)=>e.preventDefault(); // 
        document.onkeydown = function (e) { // f5,f12 keycode === 116,123
            e = window.event || e;
            var keycode = e.keyCode || e.which;
            if ([116,123].indexOf(keycode) !== -1) {  
                if (window.event) {// ie
                    try { e.keyCode = 0; } catch (e) { }
                    e.returnValue = false;
                } else {// firefox
                    e.preventDefault();
                }
            }
        }
    }
    /**
     *  
     */
    render(){
        return <Table />;
    }

}

export default Game