import React from 'react';
import style from './Timer.module.css'

/*
 
*/

export default class Timer extends React.Component {
    static defaltProps = {
        time:90
    }
    seats = '';
    
    constructor(props) {
        super(props)
        this.timer = null;
        const {time} = props 
        this.state = {
            sec : time * 60,
            ew  : 0,
            sn  : 0
        }
    }

    componentDidMount() {
        this.start('ew');
    }
    get hour(){
        let h =parseInt(this.state.sec/60 /60)
        if(h===0) return "00" ;
        if(h > 9) return h;
        return "0"+h
     }
    get min(){
       let min =parseInt(this.state.sec/60%60)
       if(min===0) return "00" 
       return min > 9 ? min : "0" + min
    }
    get sec(){
        let sec =parseInt(this.state.sec%60)
        if(sec===0) return "00" 
        return sec > 9 ? sec : "0" + sec
     }

     get ewTime(){
         const {ew} = this.state;
         var sec = ew % 60 > 9 ? ew % 60 : "0" + ew % 60
         var min = parseInt(ew/60) % 60;
         min = min>9 ? min : "0"+min
         return  min + ":" + sec
     }
     get snTime(){
        const {sn} = this.state
        var sec = sn % 60 > 9 ? sn % 60 : "0" + sn % 60
        var min = parseInt(sn/60) % 60;
        min = min>9 ? min : "0"+min
        return  min + ":" + sec
    }
    start = (seats) => {
        this.seats = seats;
        if(this.timer) clearInterval(this.timer)
        this.timer =  setInterval((seats)=>{
            let seat = this.seats
            this.setState({
                sec:this.state.sec -1,
                [seat] : this.state[seat ] + 1
            })
        },1000)
    }

    stop = () => {
        clearInterval(this.timer);
        this.timer = 0;
    }
  
    render() {
        return (
            <div>
                <div className={style.timername}>{this.props.name}</div>
                <div className={style.timer}>
                    <div>{this.hour}</div>
                    <div>{this.min}</div>
                    <div>{this.sec}</div>
                </div>
                <div className={style.timer1}>
                {this.ewTime}<br />
                {this.snTime}<br />
                </div>
            </div>
        );
    }
}
